header {
  height: 50px;
  border-bottom: 1px solid #efefef;
  line-height: 50px;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fefefe;
  padding: 0 15px;
}
header .sidebar-toggle {
  display: none;
  cursor: pointer;
  float: left;
  margin-right: 10px;
}
header .sidebar-toggle:before {
  position: relative;
  top: .4375rem;
  font-family: sans-serif;
  font-size: 2.5rem;
  line-height: .125;
  content: '\2261';
}
header .brand {
  float: left;
  padding-right: 15px;
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
  color: #555;
}
header .brand:before {
  content: " ";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
header .brand img {
  max-height: 30px;
  vertical-align: middle;
}
header nav {
  float: left;
}
header nav a,
header nav a:hover,
header nav a:focus,
header nav a:active {
  color: #565656;
}
header nav a {
  padding: 0.5em 0.75em;
  margin: 0.125em;
}
header nav a:hover,
header nav a.active {
  background: #eee;
}
@media (max-width: 720px) {
  header .sidebar-toggle {
    display: inline-block;
  }
}

.container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.container header,
.container .sidebar,
.container .content {
  position: absolute;
}
.container header,
.container .sidebar {
  z-index: 5;
}
.container .sidebar {
  top: 50px;
  left: 0;
  bottom: 0;
  width: 250px;
  transition: left 0.3s;
  border-right: 1px solid #efefef;
  background-color: #fefefe;
}
.container .content {
  left: 250px;
  top: 50px;
  right: 0;
  bottom: 0;
}
@media (max-width: 720px) {
  .container .sidebar {
    left: -250px;
    opacity: 0.8;
  }
  .container .content {
    left: 0;
  }
  .container :checked + .sidebar {
    left: 0;
  }
}

.sidemenu {
  margin-bottom: 20px;
}
.sidemenu a {
  display: block;
  color: #909090;
  margin-bottom: 2px;
  font-size: 12px;
}
.sidemenu a.selected {
  color: black;
  font-weight: bold;
  font-size: 14px;
}
.menulayoutborder {
  border-left: 1px solid #E8E8E8;
  padding-left: 0;
}


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-size: 12px;
  font-family: sans-serif;
}
body {
  background-color: #fff;
  color: #565656;
}
a {
  text-decoration: none;
}
h1,
h2,
h3 {
  font-weight: bold;
  margin-bottom: 0.35em;
}
h1.text-center small,
h2.text-center small,
h3.text-center small {
  display: block;
  line-height: 1em;
  color: #909090;
}
h1 {
  font-size: 1.4em;
  line-height: 2em;
}
h2 {
  font-size: 1.3em;
  line-height: 1.8em;
}
[type="checkbox"],
[type="radio"] {
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}

.clearfix:after,
.container:after,
.container-fluid:after,
.row:after {
  clear: both;
}
.text-center {
  text-align: center;
}

